.header{
  // display: none !important;
  background-image: url(./../icons/header-bg.png);
  background-size: cover;
  background-position: center;

  &__content {
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    color: $title-color;
    font-family: Manrope, sans-serif;
    font-size: 36px;
    font-weight: 700;
    line-height: 100%;

    @include on-tablet {
      font-size: 48px;
    }
  }

  &__bottom {
    padding-bottom: 40px;

    @include on-tablet {
      padding-bottom: 64px;
    }
  }
}
